.Button {
  padding: 10px 20px;
  color: #000;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 12px;
  display: inline-block;
  margin-right: 15px;
}

.Button:focus {
  outline: none;
}

.Button:active {
  -webkit-box-shadow: inset 2px 2px 1px rgba(0, 0, 0, 0.3);
          box-shadow: inset 2px 2px 1px rgba(0, 0, 0, 0.3);
}

.Button:disabled {
  background: #ccc;
  color: #000;
  cursor: not-allowed;
}

.error {
  background: rgba(240, 87, 108, 1);
}

.success {
  background: rgba(161, 240, 69, 1);
}

.primary {
  background: #2884f5;
  color: #fff;
}



