.QuizCreator {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 50px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: 100%;
  background: -webkit-gradient(linear, right top, left top, from(#f0613c), to(#f0783c));
  background: -webkit-linear-gradient(right, #f0613c 0%, #f0783c 100%);
  background: -o-linear-gradient(right, #f0613c 0%, #f0783c 100%);
  background: linear-gradient(270deg, #f0613c 0%, #f0783c 100%);
}

.QuizCreator > div {
  width: 100%;
  max-width: 800px;
  padding: 0 20px;
}

.QuizCreator h1 {
  text-align: center;
}

.QuizCreator form {
  background: #eee;
  border-radius: 5px;
  padding: 15px;
  -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}



