.Input {
  margin-bottom: 15px;
}

.Input label {
  margin-bottom: 3px;
  padding: 0;
  display: block;
  font-weight: bold;
}

.Input input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  border: 1px solid #bebebe;
  border-radius: 2px;
  outline: none;
  padding: 7px;
  margin: 0 0 5px;
  width: 100%;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.Input span {
  color: #f01f30;
  font-size: 12px;
  font-weight: bold;
}

.Input.invalid label {
  color: #f01f30;
}



