.MenuToggle {
  position: fixed;
  left: 40px;
  top: 40px;
  font-size: 20px;
  color: #fff;
  cursor: pointer;
  -webkit-transition: opacity, left 0.22s ease-in;
  -o-transition: opacity, left 0.22s ease-in;
  transition: opacity, left 0.22s ease-in;
  z-index: 100;
}

.MenuToggle:hover {
  opacity: 0.7;
}

.MenuToggle.open {
  left: 320px;
}



