.Auth {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding-top: 100px;
  width: 100%;
  background: -webkit-gradient(linear, right top, left top, from(#cef2fa), to(#59bfef));
  background: -webkit-linear-gradient(right, #cef2fa 0%, #59bfef 100%);
  background: -o-linear-gradient(right, #cef2fa 0%, #59bfef 100%);
  background: linear-gradient(270deg, #cef2fa 0%, #59bfef 100%);
}

.Auth > div {
  width: 100%;
  padding: 0 20px;
  max-width: 600px;
}

.Auth h1 {
  color: #fff;
  text-align: center;
}

.AuthForm {
  background: #eee;
  -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  padding: 15px;
  border-radius: 5px;
}



